import { Project } from "@/store/interface"
import request from "./request"

export async function addProject(name:string, tid:number){
    return request({
        method: 'POST',
        url: '/project/addProject',
        params:{
            name: name,
            groupId: tid
        }
    })
}

export async function searchProjectByGroup(tid:number){
    return request({
        method: 'GET',
        url: '/project/findGid',
        params:{
            groupId: tid
        }
    })
}

export async function searchProjectById(pid:number){
    return request({
        method: 'GET',
        url: '/project/findPid',
        params:{
            projectId: pid
        }
    })
}